import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LayoutComponent } from './layout/layout.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'dashboard',
    pathMatch: 'full',
  },
  {
    path: '',
    component: LayoutComponent,
    data: {
      title: 'Home'
    },
    children: [ 
      {
        path: 'addUser',
        loadChildren: () => import('./views/addUser').then(m => m.AddUserModule)
      },
      {
        path: 'addBusiness',
        loadChildren: () => import('./views/addBusiness').then(m => m.AddBusinessModule)
      },
      {
        path: 'dashboard',
        loadChildren: () => import('./views/dashboard').then(m => m.DashboardModule)
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdminRoutingModule { }
